<template>
  <div class="app">
    <musicList :title="title" :bg-image="bgImage" :songs="songs"></musicList>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapGetters } from "vuex";
// import { ERR_OK } from "api/config";
import musicList from "base/musicList/musicList";
import { getToplistDetail } from "api/rank";
const ERR_OK = 200;
export default {
  components: {
    musicList
  },
  computed: {
    title() {
      return this.disc.name;
    },
    bgImage() {
      return this.disc.coverImgUrl;
    },
    ...mapGetters(["disc"])
  },
  data() {
    return {
      id: "",
      songs: [],
      recommodsons: []
    };
  },
  created() {
    this._getSongList();
  },
  methods: {
    // 获取歌单歌曲
    _getSongList() {
      var that = this;
      if (!this.$route.params.id) {
        this.$router.push("/toplist");
        return;
      }

      getToplistDetail(this.$route.params.id).then(res => {
        // let ret = recomSongList(res);
        if (res.code === ERR_OK) {
          that.songs = res.playlist.tracks;
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
@import "assets/css/mixin.scss";
.app {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(34, 34, 34);
  z-index: 301;
}
</style>



